@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;800&family=Poppins:wght@300;400&family=Ubuntu:wght@300&display=swap");
$root_font: "Montserrat";
$breakpoint-tablet: 300px;
$breakpoint-tablet-max: 768px;
$bgc: #528078;
$bgc_secondary: #3e615b;
* {
  transition: all 300ms ease;
}
.App {
  text-align: center;
  font-family: $root_font, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
header {
  .navParent {
    min-height: 10vh;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    background-color: $bgc;
    .logo {
      font-size: 25px;
      font-weight: 300;
      color: white;
    }
    .navigation {
      ul {
        list-style: none;
        margin: 0;
        padding: 0;
        li {
          float: left;
          button {
            font-size: 18px;
            font-weight: 400;
            display: block;
            height: 100%;
            background-color: $bgc;
            color: white;
            padding: 14px 16px;
            border: none;
            outline: none;
            &:hover {
              color: rgb(185, 136, 2);
              background-color: $bgc_secondary;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
}
main {
  min-height: 90vh;
  width: 100%;
  background-image: url("./images/nature.jpg");
  background-position: top;
  background-size: cover;
}
footer {
  background-color: $bgc;
  .footerContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    @media (min-width: $breakpoint-tablet) and (max-width: $breakpoint-tablet-max) {
      flex-direction: column;
    }
    .footerChild {
      width: 30%;
      background-color: $bgc_secondary;
      padding: 10px;

      strong {
        color: $bgc;
        padding: 10px;
        background-color: rgb(207, 206, 206);
        &:hover {
          background-color: rgb(243, 242, 242);
          color: black;
          cursor: pointer;
          border-radius: 10px;
        }
      }
      @media (min-width: $breakpoint-tablet) and (max-width: $breakpoint-tablet-max) {
        margin: 20px 0;
        width: 60%;
      }
    }
  }
}
