@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;600;800&family=Poppins:wght@300;400&family=Ubuntu:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{transition:all 300ms ease}.App{text-align:center;font-family:"Montserrat",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Cantarell,"Open Sans","Helvetica Neue",sans-serif}header .navParent{min-height:10vh;width:100%;display:flex;justify-content:space-around;align-items:center;height:100%;background-color:#528078}header .navParent .logo{font-size:25px;font-weight:300;color:white}header .navParent .navigation ul{list-style:none;margin:0;padding:0}header .navParent .navigation ul li{float:left}header .navParent .navigation ul li button{font-size:18px;font-weight:400;display:block;height:100%;background-color:#528078;color:white;padding:14px 16px;border:none;outline:none}header .navParent .navigation ul li button:hover{color:#b98802;background-color:#3e615b;cursor:pointer}main{min-height:90vh;width:100%;background-image:url(/landingpage/static/media/nature.46833d4d.jpg);background-position:top;background-size:cover}footer{background-color:#528078}footer .footerContainer{display:flex;flex-direction:row;justify-content:space-around;align-items:center;padding:10px 0}@media (min-width: 300px) and (max-width: 768px){footer .footerContainer{flex-direction:column}}footer .footerContainer .footerChild{width:30%;background-color:#3e615b;padding:10px}footer .footerContainer .footerChild strong{color:#528078;padding:10px;background-color:#cfcece}footer .footerContainer .footerChild strong:hover{background-color:#f3f2f2;color:black;cursor:pointer;border-radius:10px}@media (min-width: 300px) and (max-width: 768px){footer .footerContainer .footerChild{margin:20px 0;width:60%}}

